import React, { Suspense, useState } from "react";
import Registration from "./components/Registration";
import Survey from "./components/Survey";
import Loading from "./components/Loading";
import Instrucion from "./components/Instruction";
import "./components/styles/body.css"

const axios = require('axios');
const url = window.location.origin;
//const url = "http://localhost:80"
const checkuserurl = url + "/api/checkuser/";
const getobjfileurl = url + "/api/getfile/";
const choiceurl = url + "/api/makeselection/";
const uniqueurl = url + "/api/getuniquecode/";

function App() {
  const [regPage, setRegPage] = useState(true)
  const [nextSeqId, setNextSeqId] = useState(0)
  const [userId, setUserId] = useState('')
  const [filename, setFilename] = useState()
  const [pageState, setPageState] = useState(0)
  const [uniqueCode, setUniqueCode] = useState('')

  const handleLogin = async(userName) => {
    const data = await axios.get(checkuserurl+userName)
    var jsonresp = data.data
    setUserId(userName)
    setNextSeqId(parseInt(jsonresp.nextId));
    setFilename(jsonresp.fileName)
    setUniqueCode(jsonresp.uniqueCode)
    setPageState(1)
    setRegPage(false)
  }
  const onClick = () =>{
    setPageState(2)
  }

  function webgl_support () { 
    try {
     var canvas = document.createElement('canvas'); 
     return !!window.WebGLRenderingContext &&
       (canvas.getContext('webgl') || canvas.getContext('experimental-webgl'));
    } catch(e) {
      return false;
    }
  };

  var webgltest = webgl_support();
  if (!webgltest){
    return <body>
      This survey needs support of webgl, please either switch a brower or enable webgl.
    </body>
  }

  if(pageState === 0){
    return(
      <body>
        <Registration onRegister={handleLogin}/>
      </body>
    );
  }
  
  if(pageState === 1){
    if(nextSeqId >= 20){
      return <p> You have completed all the tasks! Your unique code is {uniqueCode}. Thanks for taking this survey! </p>;
    }
    return(
      <body>
        <Instrucion onClick={onClick}/>
      </body>
    );
  }
  
  return (
    <body>
      <Suspense fallback={<Loading/>}> 
        <Survey url={getobjfileurl} nextseq={nextSeqId} userId={userId} fileNames={filename} choiceurl={choiceurl} uniqueCode={uniqueCode}/>
      </Suspense>
    </body>
  );
}

export default App;
